import { Routes } from '@angular/router';

import { BilldepositComponent } from './billdeposit/billdeposit.component';
import { BillreversalComponent } from './billreversal/billreversal.component';
export const BillRoutes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'billdeposit',
        component: BilldepositComponent,
		data: {
          title: 'Bill Deposit',
          urls: [
            { title: 'Dashboard', url: '/dashboard' },
            { title: 'Bill Deposit' }
          ]
          
        }
      },
      {
        path: 'billreversal',
        component: BillreversalComponent,
data: {
      title: 'Bill Reversal',
      urls: [
        { title: 'Dashboard', url: '/dashboard' },
        { title: 'Bill Reversal' }
      ]
      
    }}
      
    ]
  }
  
];
